export const handleResponse = async (response) => {
    if (response.ok) {
        return (await response.json());
    }
    else {
        const error = {
            status: response.status,
            error: await response.json(),
        };
        throw error;
    }
};
const handleReqWithBody = (method) => (api_url) => (path) => async (bodyData, extraHeaders) => await fetch(`${api_url}${path}`, {
    method,
    headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        ...extraHeaders,
    },
    body: JSON.stringify(bodyData),
}).then(r => handleResponse(r));
export const handleGet = (api_url) => async (path, token) => await fetch(`${api_url}${path}`, {
    method: 'GET',
    headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        ...(token !== undefined ? { 'X-AUTH-TOKEN': token } : {}),
    },
}).then(r => handleResponse(r));
export const handlePost = handleReqWithBody('POST');
export const handlePut = handleReqWithBody('PUT');
