export const GoogleLoginConfig = (element, onSuccess, onFailure) => {
    // eslint-disable-next-line no-undef
    gapi.load('auth2', () => {
        // eslint-disable-next-line no-undef
        gapi.auth2
            .init({
            client_id: process.env.REACT_APP_GOOGLE_SIGNIN_CLIENT_ID,
            cookie_policy: 'single_host_origin',
            scope: 'profile email',
        })
            .attachClickHandler(element, {}, onSuccess, onFailure);
    });
};
export const FacebookLogin = (callback, rerequest = false) => {
    // eslint-disable-next-line no-undef
    FB.login(callback, {
        scope: 'public_profile,email',
        ...(rerequest ? { auth_type: 'rerequest' } : {}),
    });
};
export class NeedsRerequestFacebookError extends Error {
    constructor(message) {
        super(message);
        this.name = 'NeedsRerequestFacebookError';
    }
}
export default {
    GoogleLoginConfig,
    FacebookLogin,
    NeedsRerequestFacebookError
};
